$primaryColor: #4527a4;

// Custom
.bdbNowEditing {
	outline: 2px solid $primaryColor;
}

.grbbPanelBody {
	.components-flex-item .components-text {
		margin-top: 10px;
	}

	.components-flex {
		align-items: baseline !important;
	}

	.components-base-control__field {
		margin-top: 10px;
	}
}

.grbb-Select {
	margin-top: 10px;
}

.grbb-toast {
	display: flex;
	align-items: center;
	column-gap: 20px;

	p {
		margin: 0;
		font-size: 11px;
		font-weight: 500;
	}
}

.ttpAuthorization {
	position: relative;
	display: flex;
	align-items: center;
	justify-content: center;
	column-gap: 10px;
	margin-bottom: 10px;

	button.ttpAuthBtn {
		border: 1px solid $primaryColor;
		box-shadow: 1px 1px 0px 1px $primaryColor;
		color: $primaryColor;
	}

	.fbAuthBtn {
		background-color: #3b5998;
		color: #fff;
	}

	.fbLogout {
		background: red !important;
		color: #fff !important;

		&:hover {
			background: #df0f0f !important;
		}
	}

}

.fbHelpText {
	text-align: center;
}

.grbbClearCacheBtn {
	font-size: 14px;
	padding: 5px 15px 5px 15px;
	border-radius: 3px;
	text-transform: uppercase;
	font-weight: 500;
	background-color: #4527a4;
	color: #fff;
	cursor: pointer;
}

.grbbGetHelpButton {
	margin-bottom: 10px;

	.bPlLabel {
		width: initial;
		flex: 1;
	}

	.grbbTarget {
		border: 1px solid #4527a4 !important;
		box-shadow: 1px 1px 0px 1px #4527a4 !important;
		padding: 2px 7px !important;
		border-radius: 3px !important;
		font-size: 14px !important;
		text-decoration: none !important;
		color: #000;
		cursor: pointer;
	}
}


.ttpProFeature {
	h3 {
		a {
			text-decoration: underline !important;
		}
	}

	>a {
		border: 1px solid #4527a4 !important;
		box-shadow: 1px 1px 0px 1px #4527a4 !important;
		padding: 5px 10px !important;
		border-radius: 3px !important;
		font-size: 14px !important;
		text-decoration: none !important;
		color: #000;
		cursor: pointer;
	}

}

.labelClearCache {
	padding: 0px 5px;
	border-radius: 5px;
	color: #fff;
	background-color: #4527a4;
}